import { Avatar, Hidden, Icon, IconButton, MenuItem, useMediaQuery } from '@mui/material';
import { Box, styled, useTheme } from '@mui/system';
import { MatxMenu, MatxSearchBox } from 'app/components';
import { themeShadows } from 'app/components/MatxTheme/themeColors';
import { NotificationProvider } from 'app/contexts/NotificationContext';
import useAuth from 'app/hooks/useAuth';
import useSettings from 'app/hooks/useSettings';
import { topBarHeight } from 'app/utils/constant';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Span } from '../../../components/Typography';
import NotificationBar from '../../NotificationBar/NotificationBar';
import SearchDrawer from 'app/components/SearchDrawer/NotificationBar';
import ShoppingCart from '../../ShoppingCart';
import Parse from 'app/vendor/Parse'

const StyledIconButton = styled(IconButton)(({ theme }) => ({
  color: theme.palette.text.primary,
}));

const TopbarRoot = styled('div')(({ theme }) => ({
  top: 0,
  zIndex: 96,
  transition: 'all 0.3s ease',
  boxShadow: themeShadows[8],
  height: topBarHeight,
}));

const TopbarContainer = styled(Box)(({ theme }) => ({
  padding: '8px',
  paddingLeft: 18,
  paddingRight: 20,
  height: '100%',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  background: theme.palette.primary.main,
  [theme.breakpoints.down('sm')]: {
    paddingLeft: 16,
    paddingRight: 16,
  },
  [theme.breakpoints.down('xs')]: {
    paddingLeft: 14,
    paddingRight: 16,
  },
}));

const UserMenu = styled(Box)(() => ({
  display: 'flex',
  alignItems: 'center',
  cursor: 'pointer',
  borderRadius: 24,
  padding: 4,
  '& span': { margin: '0 8px' },
}));

const StyledItem = styled(MenuItem)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  minWidth: 185,
  '& a': {
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    textDecoration: 'none',
  },
  '& span': { marginRight: '10px', color: theme.palette.text.primary },
}));

const IconBox = styled('div')(({ theme }) => ({
  display: 'inherit',
  [theme.breakpoints.down('md')]: { display: 'none !important' },
}));

const Layout1Topbar = () => {

  const theme = useTheme();
  const { settings, updateSettings } = useSettings();
  const { logout, getUser } = useAuth();
  const user = getUser()
  const isMdScreen = useMediaQuery(theme.breakpoints.down('md'));
  const queryParams = new URLSearchParams(window.location.search)
  const isMobile = !!queryParams.get("mobile")
  const [mobileDisplayIndex, setMobileDisplayIndex] = useState(0)
  const [messages, setMessages] = useState([])


  useEffect(() => {
    function progress() {
      setMobileDisplayIndex((last) => {
        if (last < messages.length - 1) {
          return last + 1;
        } else {
          setMobileDisplayIndex(0)
        }
      })
    }

    if (isMobile) {
      const timer = setInterval(progress, 5000)
      return () => {
        clearInterval(timer)
      }
    }

  }, [isMobile, messages])

  const updateSidebarMode = (sidebarSettings) => {
    updateSettings({
      layout1Settings: { leftSidebar: { ...sidebarSettings } },
    });
  };

  const handleSidebarToggle = () => {
    let { layout1Settings } = settings;
    let mode;
    if (isMdScreen) {
      mode = layout1Settings.leftSidebar.mode === 'close' ? 'mobile' : 'close';
    } else {
      mode = layout1Settings.leftSidebar.mode === 'full' ? 'close' : 'full';
    }
    updateSidebarMode({ mode });
  };

  return (
    <TopbarRoot>
      <TopbarContainer>
        <Box display="flex">
          {
            user && <StyledIconButton onClick={handleSidebarToggle}>
              <Icon>menu</Icon>
            </StyledIconButton>
          }


        </Box>
        {messages[mobileDisplayIndex]?.text}
        <Box display="flex" alignItems="center">
          {
            <>

              <StyledIconButton onClick={() => window?.location.assign(`https://api.whatsapp.com/send?phone=6587413159`)}>
                <Icon>phone</Icon>
                +65 8741 3159
              </StyledIconButton>

            </>
          }
          {
            user && <SearchDrawer />
          }

          {

            "" && <>
              <MatxSearchBox />

              <NotificationProvider>
                <NotificationBar />
              </NotificationProvider>


              <ShoppingCart />
            </>
          }

          {
            user && <MatxMenu
              menuButton={
                <UserMenu>
                  <Hidden xsDown>
                    <Icon> person </Icon>
                  </Hidden>
                </UserMenu>
              }
            >
              {
                <>

                  <StyledItem>
                    <Span> {user?.get('email')} </Span>

                  </StyledItem>

                  <StyledItem>
                    <Link to="/">
                      <Icon> home </Icon>
                      <Span> Home </Span>
                    </Link>
                  </StyledItem>

                  <StyledItem>
                    <Link to="/dashboard/changePassword">
                      <Icon> settings </Icon>
                      <Span> Update Password </Span>
                    </Link>
                  </StyledItem>

                  <StyledItem onClick={logout}>
                    <Icon> power_settings_new </Icon>
                    <Span> Logout </Span>
                  </StyledItem>
                </>
              }
            </MatxMenu>

          }
          {
            !user && "" &&
            <Link to="/session/signin">
              <Span> Login </Span>
            </Link>

          }
        </Box>
      </TopbarContainer>
    </TopbarRoot>
  );
};

export default React.memo(Layout1Topbar);
