import { Badge, Button, Card, Drawer, Icon, IconButton, ThemeProvider, TextField, Menu, MenuItem } from '@mui/material';
import { Box, styled, useTheme } from '@mui/system';
import useNotification from 'app/hooks/useNotification';
import useSettings from 'app/hooks/useSettings';
import { sideNavWidth, topBarHeight } from 'app/utils/constant';
import { getTimeDifference } from 'app/utils/utils.js';
import React, { Fragment, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { themeShadows } from '../MatxTheme/themeColors';
import { Paragraph, Small } from '../Typography';
import { setData } from 'app/redux/actions/ApplicationActions'
import { useDispatch, useSelector } from 'react-redux';
import NumberConsole from 'app/components/NumberConsole'
import { NumberCategory } from './NumberCategory';
import DateMenu from 'app/components/Menu'
import Parse from "app/vendor/Parse"

const Notification = styled('div')(() => ({
  padding: '16px',
  marginBottom: '16px',
  display: 'flex',
  alignItems: 'center',
  height: topBarHeight,
  boxShadow: themeShadows[6],
  '& h5': {
    marginLeft: '8px',
    marginTop: 0,
    marginBottom: 0,
    fontWeight: '500',
  },
}));

const NotificationCard = styled(Box)(({ theme }) => ({
  position: 'relative',
  '&:hover': {
    '& .messageTime': {
      display: 'none',
    },
    '& .deleteButton': {
      opacity: '1',
    },
  },
  '& .messageTime': {
    color: theme.palette.text.secondary,
  },
  '& .icon': { fontSize: '1.25rem' },
}));

const DeleteButton = styled(IconButton)(({ theme }) => ({
  opacity: '0',
  position: 'absolute',
  right: 5,
  marginTop: 9,
  marginRight: '24px',
  background: 'rgba(0, 0, 0, 0.01)',
}));

const CardLeftContent = styled('div')(({ theme }) => ({
  padding: '12px 8px',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  background: 'rgba(0, 0, 0, 0.01)',
  '& small': {
    fontWeight: '500',
    marginLeft: '16px',
    color: theme.palette.text.secondary,
  },
}));

const Heading = styled('span')(({ theme }) => ({
  fontWeight: '500',
  marginLeft: '16px',
  color: theme.palette.text.secondary,
}));

const NotificationBar = ({ container }) => {
  const { settings } = useSettings();
  const theme = useTheme();
  const secondary = theme.palette.text.secondary;
  const [panelOpen, setPanelOpen] = React.useState(false);
  const [appendList, setAppendList] = useState([])
  const [queriedList, setQueriedList] = useState([])
  const queryParams = new URLSearchParams(window.location.search)
  const endpoint = queryParams.get("endpoint")
  const { deleteNotification, clearNotifications, notifications } = useNotification();
  const [query, setQuery] = useState("")
  const dispatch = useDispatch()
  const [selectedIndex, setSelectedIndex] = useState(0)
  const handleMenuItemClick = (event, index) => {
    setSelectedIndex(index);
    setAnchorEl(null);
  }
  const allNumbers = useSelector(state => state?.application.data?.['allNumbers']) || []
  const handleDrawerToggle = async () => {
    loadData()
    setPanelOpen(!panelOpen);
  };
  const [numberDateSelection, setNumberDateSelection] = useState([])
  const [selectedNumberList, setSelectedNumberList] = useState(0)

  useEffect(async () => {
    if (endpoint) {
      const _data = await fetch(endpoint).then(e => e.json())
      const __data = _data?.data
        .filter(e => NumberCategory[selectedIndex].func(e.num))
        .filter(e => e.num.includes(query))
      console.log(_data)
      setQueriedList(__data)
    } else {
      const _nums = allNumbers
        .filter(e => NumberCategory[selectedIndex].func(e.num))
        .filter(e => e.num.includes(query))
      setQueriedList(_nums)
    }
  }, [allNumbers, query, selectedIndex, endpoint])

  const loadData = async () => {
    const result = await Parse.Cloud.run("getItems", {
      key: 'numbers',
      query: {
        search: true
      }
    })
    setNumberDateSelection(result)
  }

  useEffect(async () => {

    const numbers = numberDateSelection[selectedNumberList]?.data || []
    console.log(numbers)
    dispatch(setData('allNumbers', numbers))
  }, [selectedNumberList, numberDateSelection])


  useEffect(() => {
    dispatch(setData('live', appendList))
  }, [appendList])

  const { palette } = useTheme();
  const textColor = palette.text.primary;
  const [anchorEl, setAnchorEl] = React.useState(null);

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }


  const appendToList = (list) => {

    const numberToAppend = list.filter(e => !appendList.find(n => n.num === e.num))
    setAppendList([
      ...appendList,
      ...numberToAppend
    ])
  }

  const handleAddAll = (list) => {
    appendToList(list)
  }

  function handleClose() {
    setAnchorEl(null);
  }
  return (
    <Fragment>
      <IconButton onClick={handleDrawerToggle}>
        <Badge color="secondary" badgeContent={notifications?.length}>
          <Icon sx={{ color: textColor }}>search</Icon>
        </Badge>
      </IconButton>

      <ThemeProvider theme={settings.themes[settings.activeTheme]}>
        <Drawer
          width={'100px'}
          container={container}
          variant="temporary"
          anchor={'right'}
          open={panelOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
        >
          <Box sx={{ width: sideNavWidth }}>
            <Notification>
              <Icon color="primary">search</Icon>
              <DateMenu
                data={numberDateSelection}
                titleKey="date"
                onItem={(e, i) => {
                  console.log(i)
                  setSelectedNumberList(i)
                }}
              //renderButton={(row) => }
              />
            </Notification>
            <Button
              variant="outlined"
              aria-haspopup="true"
              onClick={(handleClick)}
              aria-owns={anchorEl ? "simple-menu" : undefined}
            >
              {
                NumberCategory[selectedIndex].name
              }
            </Button>
            <Button
              variant="outlined"
              aria-haspopup="true"
              onClick={() => handleAddAll(queriedList)}
              aria-owns={anchorEl ? "action-menu" : undefined}
            >
              Add All
            </Button>

            <Button
              variant="outlined"
              aria-haspopup="true"
              onClick={() => setAppendList([])}
              aria-owns={anchorEl ? "action-menu" : undefined}
            >
              Clear All
            </Button>
            <Menu id="simple-menu" anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose}>
              {
                NumberCategory.map((e, index) => <MenuItem
                  onClick={handleClose}
                  selected={index === selectedIndex}
                  onClick={(event) => handleMenuItemClick(event, index)}

                > {e?.name}

                </MenuItem>)
              }

            </Menu>

            <Box display="flex" alignItems={"center"}>
              <TextField
                autoFocus={true}
                type="text"
                name="firstName"
                label="Find Your Lucky Number"
                onChange={(e) => { setQuery(e.target.value) }}
                value={query}
                validators={["required"]}
                errorMessages={["this field is required"]}
              />
            </Box>
            <br />
            <NumberConsole
              dataArr={queriedList}
              pageSize={8}
              handleClick={(n) => {
                appendToList([n])
              }}
              displaySource={true}
            />
          </Box>
        </Drawer>
      </ThemeProvider>
    </Fragment>
  );
};

export default NotificationBar;
