import { abab, aaa, repeatNumber, hasNNumber, ababab, isYiJing, isPureYijing, hasNDigit, repeatExact, uniqueNumber, isLevelBYijing, isLevelCYijing } from 'app/../../shared/number'


export const NumberCategory = [
  {
    name: 'ALL',
    func: (n) => true
  },
  {
    name: 'ABAB',
    func: abab
  },
  {
    name: 'AAA',
    func: aaa
  },
  {
    name: '3D',
    func: (n) => hasNDigit(3, n)
  },
  {
    name: '13YAO',
    func: uniqueNumber

  },

  {
    name: 'ABABAB',
    func: ababab
  },
  {
    name: 'REPEAT',
    func: repeatNumber
  },
  {
    name: 'MIRROR',
    func: repeatExact
  },
  {
    name: 'mini mirror',
    func: (n) => n.split('').slice(0, 4).sort().join(',') === n.split('').slice(4).sort().join(',') && n.split('').slice(0, 4).reduce((prev, next) => Number(prev) + Number(next), 0) === n.split('').slice(4).reduce((prev, next) => Number(prev) + Number(next), 0)
  },
  {
    name: 'YiJing',
    func: isYiJing
  },
  {
    name: 'Pure YiJing',
    func: isPureYijing
  },
  {
    name: 'Level B Yijing',
    func: isLevelBYijing
  },
  {
    name: 'Level C Yijing',
    func: isLevelCYijing
  },
  {
    name: `Num`,
    func: (n) => [...Array(10).keys()].filter(e => hasNNumber(n, `${e}`, 5)).length > 0
  }

]


